<template>
	<view class="rider-list">		
		<view class="content">	
			<view class="list-wrap" v-for="(item,indexA) in waitList" :key="indexA" @click="handleDetail(indexA)">
				<view class="item-left">订单编号</view>
				<view class="item-right">
					<view class="right_title">{{item.orde.OrderNumber}}</view>
					<image src="../../static/shang.png" mode="widthFix"></image>					
				</view>
			</view>
		</view>		
		<view class="title" v-if="Merchants">没有更多订单啦!!!...</view>
	</view>
</template>
<script>
	import common from '../../common/common'
	var _self;
	export default {		
		data() {
			return {
				waitList: [],
				page:1,
				total:0,
				Merchants:false,			
				outletsid:''  // 网点id
			};
		},
		onLoad() {
			_self = this;
			let { F_CompanyId } = uni.getStorageSync("riderinfo")
			_self.outletsid = F_CompanyId
			// 页面进来就是跳转到待打单
			_self.getRuderlist(1,_self.outletsid);  //初始话网点下的全部数据
		},
		onReachBottom(){      // 页面触底事件
			_self.page += 1;   // 当前页数
			if(_self.page > _self.total){				
				_self.Merchants = true;				
				return
			}				
			_self.getRuderlist(_self.page,_self.outletsid);
		},
		methods: {				
			getRuderlist(page,outletsid) {	// 获取一些订单		
				common.request({
					path: "UserOrder/getpage",
					data: {
						outletsid: outletsid,   // 网点id
						orderstate: '4',   // 订单状态  全部0 / 待结算 1 / 待出票 2 / 待领取 3 / 已完成 4 / 关闭订单 5
						rows: 10,
						page: page,
						sidx: "LssueTime",
						sord: "desc",					
					},
					success: res => {
						_self.waitList = [..._self.waitList,...res.data.rows];	
						_self.total = res.data.total
					}
				})
			},
			// 点击查看已完成订单
			handleDetail(i) {						
				uni.navigateTo({
					url: './merchantHistoryDetails?waitList=' + encodeURIComponent(JSON.stringify(_self.waitList[i]))
				});
			}
		}
	}
</script>
<style lang="scss" scoped>
	.title{
		width: 100%;
		text-align: center;
		padding: 40rpx 0;
		color: #ccc;
	}	
	.rider-list {
		.nav {
			height: 13vh;
			display: flex;
			flex-direction: row;
			align-items: center;
			border-bottom: 1px solid #eee;
			background-color: #FFFFFF;
		}
		.tabs {
			width: 80vw;
			margin: 20rpx auto 0;
		}
		.list-wrap {	
			margin-top: 15rpx;
			display: flex;
			background-color: #fff;
			justify-content: space-between;
			align-items: center;
			padding: 30rpx;
			height: 70rpx;
			border-radius: 8rpx;
			.item-left {
				font-size: 16px;
				color: #666;
			}
			.item-right {
				display: flex;
				align-items: center;				
				color: $color;
				.right_title{
					font-size: 14px;
					color: #666;
					margin-right: 10rpx;
					width: 430rpx;
					height: 100%;
					overflow: hidden;
					text-align: right;
				}
				image{
					width: 20rpx;
					height: 20rpx;
					transform: rotate(90deg); 
				}
			}						
		}
	}
	.item-left {
		display: flex;
		flex-direction: column;
		justify-content: space-between;	
		.item-adress {
			text {
				font-size: 32rpx;
			}
		}	
		.item-user {
			color: #999;	
			.user-phone {
				margin-left: 20rpx;
			}
		}
	}
	.item-right {
		display: flex;
		align-items: center;
		color: $color;
	}		
</style>
